function Pago(/*{ token, pagoId }*/) {
    let params = new URLSearchParams(document.location.search);
    const paymentId = params.get("paymentId");
    const token = params.get("token");
    const payerId = params.get("PayerID");

    const generarToken = async () => {
        var myHeaders = new Headers();
        myHeaders.append("Accept", "application/json");
        myHeaders.append("Accept-Language", "en_US");
        myHeaders.append("Authorization", "Basic QWR5YXJvTEtUSFFEWEh6T3NKZTZheG1TR3oxS0V5WlRUaHlaa3BTa0o2MTFCdXJKQTR5S2lWdVNVZ2RVZEVfMFUtWXlTZmhTaEs2eGhOY0s6RUczZXpMRDkxekFFcTVfZlRXUTNqNkdFdVhyLTJibXZ3bjdJTi0tRzY5Q1pFQi1rQ0l0RXFMQ1FOTGJPTjR4VVNldG1tZjNqX0F1TU1QSXc=");
        myHeaders.append("Content-Type", "application/x-www-form-urlencoded");

        var urlencoded = new URLSearchParams();
        urlencoded.append("grant_type", "client_credentials");

        var requestOptions = {
            method: 'POST',
            headers: myHeaders,
            body: urlencoded,
            redirect: 'follow'
        };

        fetch("https://api-m.sandbox.paypal.com/v1/oauth2/token", requestOptions)
            .then(response => response.text())
            .then(result => {
                const token = JSON.parse(result).access_token;
                return token;
            });
    }


    const obtenerPago = async (mToken, mPagoId) => {
        var myHeaders = new Headers();
        myHeaders.append("Content-Type", "application/json");
        myHeaders.append("Authorization", `Bearer ${mToken}`);

        var requestOptions = {
            method: 'GET',
            headers: myHeaders,
            redirect: 'follow'
        };

        const res = await fetch(`https://api.sandbox.paypal.com/v1/payments/payment/${mPagoId}`, requestOptions);
        const data = await res.json();

        return data;
    }

    const ejecutarPago = () => {
        let reqBody = {
            paymentId,
            payerId
        }
        console.log(reqBody);
        try {
            fetch('/api/facturas/pagos', {
                method: 'POST',
                headers: {
                    'Content-Type': 'application/json'
                },
                body: JSON.stringify(reqBody)
            }).then(res => {
                if (!res.ok) {
                    return res.text().then(text => { throw new Error(text) })
                }
                else {
                    console.log("OKAY")
                }
            })
                .catch(err => {
                    console.log(err.toString());
                });

        } catch (error) {
            alert("ERROR");
        }
    }

    //let miToken = generarToken();
    //const pago = obtenerPago();

    ejecutarPago();


    return (
        <>
            <div>
                <p>Pago realizado correctamente</p>
                <p>{paymentId}</p>
                <p>{payerId}</p>
            </div>
        </>
    );
}

export default Pago;
